/* Estilos para el componente SuccessPage */

.backgoundSuccess{
    width: 100%;
    height: 27vh;
    background: linear-gradient(135deg, #000000, #001429);
    display: flex;
    justify-content: center;
    align-items: center;
}

.success-page-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  height: auto;
  min-height: 90vh;
  margin-top: 5vh;
  text-align: start;
}

.ticket {
  background-color: #f9f9f9;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 24px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  max-width: 500px;
  margin: 20px auto;
}

.ticket h4 {
  font-size: 1.8dvh;
  color: #333;
  margin-bottom: 16px;
  text-align: center;
}

.ticket p {
  font-size: 1.6vh;
  color: #333;
  margin-bottom: 16px;
  text-align: start;
}

.ticket ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.ticket li {
  padding: 16px;
  border-bottom: 1px solid #e0e0e0;
  font-size: 1rem;
  color: #555;
}

.ticket li:last-child {
  border-bottom: none;
}

.ticket strong {
  color: #333;
}

.ticket p {
  font-size: 1.2rem;
  color: #333;
  text-align: right;
  margin-top: 24px;
}

.ticket p strong {
  font-weight: bold;
  color: #000;
}

  
.company-logo {
    width: 100%;
    height: 100%;
    object-fit: cover;
    max-width: 20vh;
    margin-bottom: 20px;
}

.h2-success {
    font-size: 3vh;
    margin-top: 10vh;
    color: white;
}

.p-success {
    font-size: 1rem;
    margin-bottom: 20px;
    color: #666;
}

.download-button {
    padding: 10px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #2C3360;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-bottom: 20px;
}

.download-button:hover {
    background-color: #0056b3;
}

.home-link {
    font-size: 1rem;
    color: #2C3360;
    text-decoration: none;
    transition: color 0.3s ease;
}

.home-link:hover {
    color: #0056b3;
}

.failed-transaction{
    margin-bottom: 10vh;
}

/* Estilos responsivos */
@media (max-width: 768px) {
    .h2-success {
        text-align: center;
    }

    .p-success {
        text-align: center;
    }

    .download-button {
        font-size: 0.875rem;
        padding: 8px 16px;
    }

    .home-link {
        font-size: 0.875rem;
    }
}