.header-container {
  padding: 0;
  margin: 0;
  width: 100%;
  top: 0;
  position: sticky;
  z-index: 50000;
}

.header {
  width: 100%;
  top: 0;
  padding: 0vh 10vh;
  position: absolute;
  transition: background-color 0.2s ease-in-out; 
  background:none;
}

.navbar {
  width: 100%;
  display: flex;
  justify-content: space-between; 
  align-items: center;
  top: 0;
  z-index: 100;
  border-radius: 10px;
  transition: background-color 0.3s ease; 
}

.menu-hamburguer{
  width: 100%;
  flex: 1;
}

.logo {
  margin: 0;
  padding: 0;
  flex: 1;
  text-align: center;
  font-weight: bold;
  color: white;
  transition: color 0.3s ease; 
}

.logotype{
  margin: 0;
  padding: 1vh 0;
  height: 10vh;
}

.nav-links-container{
  flex: 1;
}

.nav-links {
  width: 100%;
  list-style-type: none;
  display: flex; 
  align-content: center;
  align-items: center;
  margin-top: -1dvh;
  padding: 2vh 0vh 0 25vh;
}

.links-social{
  display: flex;
  flex-direction: row;
  gap: 2vh;
}

.links-social {
  color: inherit;
  text-decoration: none;
  transition: color 0.3s ease, box-shadow 0.3s ease; /* Transición suave */
  padding: 8px; /* Espacio interno para dar forma circular */
  border-radius: 50%; /* Forma circular */
  display: flex;
  align-items: center;
  justify-content: center;
}

.links-social:hover {
  color: #333;
  filter: brightness(1.2);
}

.nav-links li {
  margin-left: 20px; 
}

.nav-links a {
  text-decoration: none;
  color: white;
  font-weight: 800;
  transition: color 0.3s ease;
}

.links-social a.whatsapp:hover {
  color: #25D366; 
}

.links-social a.instagram:hover {
  color: #E4405F; 
}

/* Efecto de hover en los enlaces */
.nav-links a:hover {
  color: lightgray;
}

.cart-icon{
  position: relative;
  margin: 0;
  font-size: 2.5vh;
  padding: 0 0 0 7vh;
  align-items: center;
}

.icon-carrito-prev:hover{
  cursor: pointer;
}


/* SCROLLED */
.header.scrolled {
  background-color: white; 
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05);
}

.scrolled-navlinks a{
  color: black;
}

.scrolled-cart{
  color: black;
}


/*CART*/

.cart-icon {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  min-width: 3rem;
  font-size: 3vh;
  color: whitesmoke;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s ease;
  align-content: center;
}

.cart-icon:hover{
  color: #FAB71E;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s ease;
}

.notification {
  position: absolute;
  display: inline-block;
  border-radius: 50%;
  transform: translate(1vh, 3vh);
  font-size: 1rem;
  text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.197);
  color: #FAB71E;
  font-weight: bolder;
  text-align: center;
  line-height: 2.5dvh;
}

.notification::after {
  content: attr(data-number); 
  position: absolute;
  color: inherit;
  text-shadow: 0px 0px 5px rgba(0, 0, 0, 0);
}

@media (max-width: 768px) {
  .header{
    padding: 0.5vh 0;
  }

  .logotype{
    margin: 0;
    padding: 1vh 0;
    height: 8vh;
  }

  .nav-links {
    flex-direction: row; 
    padding: 0; 
  }

  .navbar a {
    font-size: 1rem; 
    margin: 0.5rem 0;
  }

  .links-nav{
    display: none;
  }

  .cart-icon {
    width: 100%;
    font-size: 4.22vh;
    padding-right: 1vh;
  }

  .notification {
    transform: translate(2vh, 3.4vh);
  }  

  .icon-carrito-prev{
    transform: translatey(0.8dvh);
  }

}
