/* BREAD CRUM */

.bread-crum {
  width: 100%;
  height: 7vh;
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  align-items: center;
  align-content: center;
  gap: 2vh;
  padding: 0 10vh;
  background-color: #f9f9f9; 
  text-transform: uppercase;
}

.bread-crum a {
  text-decoration: none; 
  color: #333; 
  font-size: 1.7vh; 
  line-height: 1.6;
  letter-spacing: 1px;
  font-weight: bold; 
}

.bread-crum span {
  text-decoration: none; 
  color: #333;
  font-size: 1.8vh; 
  font-weight: bold; 
}

.bread-crum a:hover {
  color: #007bff; 
}

.bread-home {
  font-size: 1.8vh;
  font-weight: bold;
}

.bread-category .bread-product{
  font-size: 1.8vh;
  font-weight: normal; 
  color: #666;
}

.em-home{
  margin-left: 1vh;
}

.em-before{
  margin: 0 1vh 0 0 ;
}

.em-later{
  margin: 0 0 0 1vh;
}

.bread-section {
  font-size: 1.8vh;
  font-weight: normal; 
  color: #666; 
}

.bread-product:last-of-type {
  font-weight: bold; /* Resaltar la última categoría */
  color: #FF6B6B; /* Negro para la última parte del breadcrumb */
}
  
/*QUERIES*/

@media (max-width: 768px) {
  .bread-crum {
    width: 100%;
    height: 7vh;
    gap: 0.4dvh;
    padding: 0 1vh;
  }

  .bread-crum a {
    font-size: 1.2vh; 
    letter-spacing: 1px;
    text-overflow: ellipsis;  
    white-space: nowrap;    
    overflow: hidden;    
    line-height: 1.4;
  }

  .em-home{
    margin-left:0;
  }
  
  .em-before{
    margin: 0 ;
  }
  
  .em-later{
    margin: 0 ;
  }

}