/* Estilos generales del contenedor del producto */
.recommended-product-card {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1vh 0;
  transition: transform 0.3s ease;
  background-color: none;
}

/* Imagen de la galería */
.image-gallery-container {
  width: 100%;
  height: auto;
  overflow: hidden;
  margin-bottom: 0.5vh;
}

.recommended-product-image {
  width: 100%;
  object-fit: cover;
  height: auto;
  min-height: 45vh;
  border-radius: 0.2vh;
}

/* Información del producto */
.recommended-product-info {
  height: 20%;
}

.recommended-product-title {
  font-size: 2vh;
  margin: 0;
  color: #333;
}


.content-price-title {
  display: flex;
  flex-direction: column;
  text-align: justify;
}

.content-price-card {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 0.8vh;
}

.product-price-original-item s{
  opacity: 0.6;
  text-decoration: line-through;
}

.content-price-card p {
  font-size: 1.2rem;
  color: #333;
  font-weight: 600;
}

.price-original {
  color: #999;
  text-decoration: line-through;
  margin-right: 8px;
}

.price-discount {
  color: #e53935;
  font-weight: bold;
}

.price-regular {
  color: #333;
  font-weight: bold;
}

/* Estilo del link */
.product-link {
  text-decoration: none;
  color: inherit;
  width: 100%;
}

.product-link:hover .recommended-product-title {
  text-decoration: underline;
}

/* Responsividad */


@media (max-width: 480px) {
  .recommended-product-card {
      padding: 8px;
      margin: 8px;
  }

  .recommended-product-title {
      font-size: 1rem;
  }

  .content-price-card p {
      font-size: 0.9rem;
  }

  .image-gallery-container {
      max-width: 100%;
  }

  
  .flechasOnMobile .p-galleria-item-left,
  .flechasOnMobile .p-galleria-item-right {
      display: none !important;
  }
}

/*INDICATORS*/
.p-galleria-indicators{
  gap: 1vh;
}

.p-galleria-indicator .p-link{
  height: 1vh;
  width: 1vh;
}
