.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #333; /* Fondo claro para resaltar el modal */
  padding: 20px;
}

.modal-content {
  background-color: white; /* Fondo blanco para el contenido */
  border-radius: 10px; /* Bordes suaves */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Sombra ligera */
  padding: 40px;
  max-width: 400px;
  width: 100%;
  font-family: 'Poppins', sans-serif; /* Fuente moderna */
}

.modal-content form {
  display: flex;
  flex-direction: column;
}

.modal-content h2 {
  text-align: center;
  color: #333;
  font-weight: 600;
  margin-bottom: 20px;
}

.modal-content label {
  font-size: 14px;
  color: #666;
  margin-bottom: 8px;
}

.input-user, input[type="password"] {
  width: 100%;
  padding: 12px;
  border-radius: 5px;
  border: 1px solid #ddd;
  margin-bottom: 20px;
  font-size: 14px;
  transition: border-color 0.3s ease;
}

.input-user:focus, input[type="password"]:focus {
  border-color: #333; /* Cambia el borde al hacer foco */
  outline: none;
}

.btn-login {
  background-color: #ff6b6b; /* Botón con color principal de Restyle */
  color: white;
  padding: 12px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  transition: background-color 0.3s ease;
}

.btn-login:hover {
  background-color: #e65555; /* Color de hover para el botón */
}

@media (max-width: 480px) {
  .modal-content {
    padding: 20px;
    max-width: 100%;
  }

  .input-user, input[type="password"] {
    font-size: 12px;
    padding: 10px;
  }

  .btn-login {
    font-size: 14px;
  }
}
