/* Global Styles */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #FF6B6B;
}

.homepage {
  width: 100%;
  overflow-x: hidden;
}

/* Animación de entrada */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.hero-section {
  position: relative;
  height: 100%;
  max-height: 90vh;
  min-height: 40vh;
  padding: 15vh 0 6vh 0;
  background: url('../../public/backLocal.webp') center/cover no-repeat;
  display: flex;
  background-size: cover;
  background-position: 1;
  justify-content: space-around;
  align-items: center;
  color: #fff;
  text-align: center;
}

.hero-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.393); /* Superposición de color oscuro */
  z-index: 1;
}

.hero-content {
  width: 100%;
  position: relative;
  z-index: 2;
  padding-left: 15vh;
  text-align: start;
  animation: fadeIn 1.5s ease-in-out; /* Animación de entrada */
}

.hero-content h1 {
  font-size: 3.8vh; 
  margin: -2vh 0 3vh 0;
  color: white; 
  font-weight: bold;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3); 
  letter-spacing: 1px; 
  transform: translateY(-2px); 
  transition: transform 0.3s ease; 
}

.hero-content h2 {
  font-size: 2.8vh;
  margin: 1vh 0 2vh; 
  font-weight: 600; 
}

.hero-content h3 {
  font-size: 2.5vh;
  margin: 2vh 0 4vh;
  font-weight: 500;
  color: #F0F0F0; /* Acentuando el texto */
}

.cta-button {
  padding: 1vh 2vh;
  background-color: #333;
  color: white;
  font-size: 2vh;
  border: none;
  border-radius: 0.4; 
  font-weight: bold;
  text-decoration: none;
  cursor: pointer;
  transition: background 0.3s;
}

.cta-button:hover {
  background-color: #f0f0f0;
  color: #333;
}

/* HERO IMAGE */
.hero-image {
  height: 100%; 
  width: 100%;
  border-radius: 0.4; 
  padding: 0vh 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.hero-card {
  background-color: #1C1C1C; 
  margin-top: 2vh;
  padding: 5vh 3vh; 
  border-radius: 0.4; 
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.3);
  width: 100%;
  height: auto; 
  gap: 2vh;
  max-width: 500px;
  color: whitesmoke;
  display: flex;
  flex-direction: column;
  justify-content: center; 
  text-align: center;
}

/* Lista de pasos */
.hero-card ul {
  list-style: none;
  padding-top: 1vh;
  padding-left: 2vh; 
  color: #F0F0F0; 
  font-size: 1.8dvh;
}

.hero-card p{
  font-size: 2vh;
  line-height: 2.6vh;
  letter-spacing: 0.5px;
}

.hero-card ul li{
  padding-bottom: 2vh;
}

.hero-card a {
  display: inline-block;
  font-size: 2dvh;
  padding: 1dvh 2dvh;
  background-color: #FF6B6B;
  color: #fff;
  font-weight: 600;
  text-decoration: none;
  border-radius: 0.4; 
  transition: background-color 0.3s ease;
}

.hero-card a:hover {
  background-color: white;
  color: #333;
}

.hero-content,
.hero-image {
  position: relative;
  z-index: 1; 
}


/*CATEGORIES*/

.categories-grid {
  padding: 8vh 2vh;
  text-align: center;
}

.categories-grid h2 {
  font-size: 2dvh;
  margin-bottom: 5vh;
  font-weight: bold;
  line-height: 1.5;
  letter-spacing: 0.1vh;
  color: #333;
}

.categories-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 20px;
}

.category-link {
  text-decoration: none;
  color: inherit;
}

.category-card {
  background-color: black;
  padding: 20px;
  border-radius: 0.4vh; 
  border: 1px black;
  border-style: groove;
  transition: transform 0.2s;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  min-height: 100px;
}

.category-card:hover {
  transform: scale(1.01); /* Efecto de zoom al pasar el ratón */
}

.category-card h3 {
  font-size: 1.8vh;
  font-weight: 500;
  line-height: 1.5;
  letter-spacing: 0.1vh;
  color: white;
}



/* Sección de Promociones */
.promo-section {
  width: 100%;
  text-align: center;
  position: relative;
  height: 100%;
  overflow: hidden;
  padding-bottom: 10vh;
}

.promo-grid {
  position: relative;
  z-index: 1; 
}

.promo-grid {
  display: flex;
  gap: 0vh;
  justify-content: center;
  flex-wrap: nowrap; 
  margin-top: 3vh;
  margin-bottom: 3vh;
}

.promo-item {
  position: relative;
  flex: 1 1 33.33%; 
  text-align: center;
  z-index: 1; 
  border-radius: 0.4vh; 
  padding: 1dvh; 
  transition: transform 0.3s;
}

.links-categoria{
  color: #333;
  text-decoration: underline;
}

.links-categoria:hover{
  color: #FF6B6B;
}

.promo-item img {
  width: 100%;
  flex: 1;
  height: 100%;
  object-fit: cover;
  border-radius: 0.4vh;
  margin-bottom: 10px;
}

/* Texto de la promoción */
.promo-item h3 {
  flex: 1;
  font-size: 1.6vh;
  font-weight: 600;
  line-height: 1.5;
  letter-spacing: 0.1vh;
  text-transform: uppercase;
  color: #333; 
}

/* Sección de Información */
.info-section {
  width: 100%;
  padding: 5vh 0 8vh;
  text-align: center;
  background-color: #333; 
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1); 
}

/* Título de la sección */
.info-section h2 {
  font-size: 3.5vh;
  margin-bottom: 40px;
  color: white;
  font-weight: 500;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3); 
  letter-spacing: 1px; 
  transform: translateY(-2px); 
}

/* Contenedor del grid */
.info-grid {
  padding: 0 10vh;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: nowrap; 
  gap: 2vh;
}

/* Elemento de la información */
.info-item {
  position: relative;
  text-align: center;
  z-index: 1; 
  border-radius: 0.4vh; 
  padding: 20px; 
  width: 100%; 
  transition: transform 0.3s, box-shadow 0.3s; 
}

.info-item:hover {
  transform: translateY(-2px); /* Efecto de elevación al pasar el ratón */
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2); /* Aumentar sombra en hover */
}

/* Ícono de la información */
.info-icon {
  font-size: 40px; 
  color: #FF6B6B; 
  margin-bottom: 15px;
}

/* Texto de la información */
.info-item h3 {
  font-size: 2vh;
  margin-bottom: 10px;
  color: white;
}

.info-item p {
  font-size: 1.5vh;
  color: white;
  text-align: center;
}

/* Featured Products Section */
.featured-products-section {
  padding: 8vh 10vh;
  margin: 0;
  text-align: center;
  position: relative;
}

.featured-products-section h2 {
  font-size: 3vh;
  margin-bottom: 5vh;
  font-weight: bold;
  line-height: 1.5;
  letter-spacing: 0.1vh;
  transform: translateY(-2px); 
  color: #333;
}


.swiper-button-prev:after, .swiper-button-next:after {
  font-family: swiper-icons;
  font-size: var(--swiper-navigation-size);
  color: #1C1C1C;
  opacity: 0;
  padding: 2dvh;
  transition: opacity 0.3s ease, color 0.3s ease; 
}

.featured-products-section:hover .swiper-button-prev:after,
.featured-products-section:hover .swiper-button-next:after {
  opacity: 1; 
  color:#1C1C1C 
}

.swiper-pagination{
  margin-bottom: -1dvh;
}

.product-card-featured {
  padding: 20px;
  background-color: #fafafa;
  border-radius: 0.4vh;
  text-align: center;
}

.product-card-featured img {
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 0.4vh;
  margin-bottom: 10px;
}

.product-card-featured h3 {
  font-size: 2vh;
  color: #333;
  margin-bottom: 10px;
}

.product-card-featured p {
  font-size: 1.1em;
  color: #333;
  margin-bottom: 2vh;
}

/* Newsletter Section */
.newsletter-section {
  padding: 6vh 4vh 9vh;
  background-color: #FF6B6B;
  color: #333;
  text-align: center;
}

.newsletter-content h2 {
  font-size: 4vh;
  margin-bottom: 2vh;
}

.newsletter-form{
  display: flex;
  width: 100%;
  justify-content: center;
  padding-top: 4vh;
  gap: 5vh;
  flex-wrap: wrap;
}

.newsletter-form input {
  min-width: 30vw;
  padding: 1vh 2vh;
  font-size: 1.8vh;
  margin-right: 10px;
  border-radius: 0.4vh; 
  border: none;
}

.btn-newsletter{
  padding: 10px 20px;
  background-color: #333;
  color: #FF6B6B;
  font-size: 2vh;
  border: none;
  border-radius: 0.4vh; 
  font-weight: bold;
  cursor: pointer;
  transition: background 0.3s;
}

.btn-newsletter:hover {
  background-color: #f0f0f0;
}


/* Responsividad */
@media (max-width: 768px) {
  .hero-section {
    flex-direction: column;
  }

  .hero-section {
    padding: 10vh 1vh;
  }

  .hero-content {
    display: none;
  }

  .hero-image {
    padding: 0;
  }

  .promo-grid {
    flex-wrap: nowrap; 
    flex-direction: column;
  }

  .info-grid {
    padding: 0 1vh;
    justify-content: center;
    flex-wrap: wrap;
    gap: 2vh;
  }

  .featured-products-section {
    padding: 60px 1vh;
    margin: 0;
    text-align: center;
  }
  
  .swiper-button-prev:after, .swiper-button-next:after {
    opacity: 1;
  }

}
