/* filter.css */

.filter-container {
  position: relative;
}

.filter-button {
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: transform 0.3s ease;
}

.filter-button.active {
  transform: rotate(90deg);
}

.bar {
  width: 4dvh;
  height: 3px;
  background-color: #fff;
  margin: 3px 0;
  transition: all 0.3s ease;
}

.menu-scrolled{
  background-color: black;
}

.filter-dropdown-list {
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #333;
  border-radius: 5px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  padding: 10px 0;
  list-style: none;
  margin: 0;
  animation: fadeIn 0.3s ease;
}

li{
  
  text-decoration: none;
}

a{
  
  text-decoration: none;
}

.a-filter{
  color: #fff;
}

.filter-dropdown-list li {
  padding: 10px 20px;
  color: #fff;
  cursor: pointer;
  transition: background 0.3s ease;
}

.filter-dropdown-list .li-filter:hover {
  background: rgba(255, 255, 255, 0.1);
}

/* Estilos para el icono de flecha del dropdown */
.dropdown-icon {
  margin-left: 1.5vh;
  padding-top: 0.2vh;
  transition: transform 0.3s ease;
}

.dropdown-icon.open {
  transform: rotate(180deg); /* Gira la flecha cuando está abierto */
}

.sub-dropdown-list {
  display: block;
  list-style: none;
  border-radius: 5px;
  padding: 5px 0;
  margin: 0.5vh 0 0 1.5vh;
  animation: fadeIn 0.3s ease;
  text-decoration: none;
}

.sub-dropdown-list li {
  padding: 8px 15px;
  margin: 0.8vh 0;
  color: #fff;
  cursor: pointer;
  text-decoration: none;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 768px) {
  .filter-dropdown-list {
    width: 100vw;
    text-align: start;
    margin-top: 2.9vh;
  }

  .filter-dropdown-list li,
  .sub-dropdown-list li {
    width: 100%;
    padding: 10px 20px;
  }
}
