.carrito {
  padding: 0;
  border: 1px solid #ffffff;
  border-radius: 0.4vh;
  width: 100%;
  height: 100%;
  z-index: 55000;
  display: flex;
  flex-direction: column;
  background-color: #ffffff; 
}

.cart-title-no {
  font-family: "Roboto Condensed", sans-serif;
  font-size: 2vh;
  justify-content: center;
  text-align: center;
  padding: 2rem;
  color: #4682B4;
  font-weight: bold; 
}

.Option {
  text-decoration: none;
  color: #4682B4;
  width: 100%;
  text-align: center;
  margin-top: 5rem;
  font-size: 1.8vh; 
}

.product-image {
  max-width: 8vh;
  max-height: 8vh;
  background-size: cover;
  object-fit: cover;
  border-radius: 0.4vh;
}

.product-image img {
  width: 9vh;
  height: 100%;
  border-radius: 0.4vh;
  transition: transform 0.2s; /* Añadir un efecto de zoom */
}

.product-image img:hover {
  transform: scale(1.05); /* Zoom al pasar el ratón */
}

.precio {
  display: flex;
  width: 100%;
  height: 25%;
  padding: 5px 0 2vh;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  text-align: start;
}

.precio h3 {
  font-size: 1.8vh;
  color: #333;
  font-weight: bold;
  margin: 10px 0;
}

.checkout {
  width: 100%;
}

.checkout button {
  padding: 1vh;
  width: 100%;
  font-size: 1.8vh;
  background-color: #0056b3;
  font-weight: bold;
  color: #fff;
  border: none;
  border-radius: 0.4vh;
  cursor: pointer;
  transition: background-color 0.5s ease-in-out, transform 0.1s ease; /* Añadir transición para el tamaño */
}

.checkout button:hover {
  background-color: #0251ac;
  transform: translateY(-0.8px); /* Efecto de elevación al pasar el ratón */
}

.CartPage {
  height: 80%;
  overflow-y: auto;
  list-style: none;
  padding: 0;
  width: 100%;
}

.CartPage ul {
  width: 100%;
  margin: 0;
  padding: 0;
}

.CartPage ul li {
  width: 100%;
  height: auto;
  padding: 1vh;
  border-radius: 0.4vh;
  margin: 0;
  flex-wrap: nowrap;
  background-color: #26262605;
}

.CartItem {
  padding: 0;
  margin: 0;
  width: 100%;
  list-style: none;
  overflow-x: hidden;
  border-bottom: 1px solid #e0e0e0; 
}

.lis {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  gap: 2vh;
}

.divContent {
  width: 100%;
  height: max-content;
  text-align: start;
}

.price-text-content {
  width: 100%;
  display: flex;
  padding-top: 2vh;
  flex-direction: row;
  justify-content: space-between;
}

.price-text-content span{
  padding-left: 1vh;
}

.color-selected{
  color: #333333c5;
  font-size: 1.5vh;
  text-transform: capitalize;
  height: 100%;
}

.quantity-items {
  display: flex;
  align-items: center;
  border-radius: 0.4vh;
  padding: 0.5em 1em;
}

.quantity-items p {
  margin: 0;
  font-weight: bold;
  color: #666;
}

.quantity-items em {
  font-size: 2vh;
  color: #333;
  padding-left: 0.5vh;
}

.divContent p {
  color: #333;
  font-size: 1.8vh;
  margin-bottom: 0px;
  text-transform: uppercase;
}

.divContent .p-text {
  font-size: 1.8vh;
  font-weight: 600;
  margin-bottom: 0.5vh;
}

.container-total-price {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around; 
  align-items: center;
}

.container-total-price h3 {
  width: auto;
}

.container-total-price .first-child-total-price em {
  margin-left: 1vh;
}

.buttonClearAll {
  display: flex;
  align-items: center;
  color: #333;
  padding: 0.5vh 1vh;
  border: none;
  font-weight: bold;
  border-radius: 0.5vh;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.1s ease; /* Efecto de transformación */
  font-size: 1.5vh;
}

.buttonClearAll:hover {
  background-color: #E85A32;
}

.delete-cart-icon {
  margin-right: 8px;
  font-size: 1.5vh;
}

.buttonClearAll:active {
  transform: scale(0.98); /* Efecto de pulsación al hacer clic */
}

.buttonClear {
  font-size: 1.8vh;
  color: #E85A32;
  max-width: 200px;
  background: none;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s ease;
}

.buttonClear:hover {
  color: red;
  background: none;
}
