footer {
  background-color: black;
  color: white;
  padding: 20px;
  text-align: center;
  margin-top: auto;
}

.footer-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-content p{
  font-size: 1.5vh;
}

.social-icons{
  width: 100%;
  font-size: 2vh;
  max-width: 20vh;
  display: flex;
  justify-content: space-evenly;
}

.social-icons a {
  color: white;
  text-decoration: none;
}
  