.galleria-container {
  width: 100%;
  max-width: 45vw;
}

.detail-image {
  max-width: fit-content;
  max-height: fit-content;
  object-fit: contain;
  margin-bottom: 10px;
}

.items-main-gallery {
  position: relative; 
  width: 100%;
  overflow: hidden;
}

.galleria-image {
  width: 100%;
  height: 100%;
  max-height: 70vh;
  object-fit: contain;
  transition: transform 0.5s ease-in-out, opacity 0.6s ease-in-out; 
  cursor: pointer; 
}

/* Efecto de zoom activado */
.galleria-image:hover {
  transform: scale(1.03); 
}

/* Lupa que aparece inicialmente */
.zoom-icon {
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  pointer-events: none; 
  cursor: pointer; 
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 3rem;
  color: rgba(255, 255, 255, 0.8);
}

.zoom-icon:hover {
  opacity: 1;
}

/* Efecto de lupa que sigue al cursor */
.loupe-effect {
  position: absolute;
  width: 10vh;
  height: 10vh;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  overflow: hidden;
  pointer-events: none; /* No interactuar con el puntero */
  transform: scale(2); /* Inicialmente no visible */
  transition: opacity 0.2s ease-in-out;
  opacity: 1;
  z-index: 10;
}

.thumbnailTemplate {
  max-height: 15vh;
  width: 100%;
  align-items: center;
  object-fit: cover;
  padding: 0;
}

.p-galleria-thumbnail-item {
  display: flex;
  overflow: hidden;
  width: 100%;
  height: 100%;
  margin: 0.2vh auto;
  align-items: center;
  justify-content: center;
}

.p-galleria-thumbnail-item img{
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.p-galleria-thumbnail-items-container {
  overflow: hidden;
  width: 100%;
  height: 100%;
  margin-right: 1vh;
}

.p-galleria-thumbnail-wrapper{
    overflow: hidden;
}

.p-galleria-thumbnail-container{
  background-color: transparent;
  width: 100%;
  height: 100%;
  justify-content: center;
}

.p-galleria-thumbnail-prev{
  color: #444;
}

.p-galleria-thumbnail-next{
  color: #444;
}

.p-galleria-thumbnail-container{
  margin: 0;
  height: 100%;
  width: 100%;
}

.thumbnailTemplate img {
  object-fit: cover;
  height: 15vh;
  width: 100%;
}

.p-galleria-item-wrapper{
  width: 100%;
  align-items: center;
  justify-content: center;
  
}

/*ARROWS*/
.custom-arrow::before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-style: solid;
}

.custom-next-arrow::before {
  border-width: 8px 0 8px 12px;
  border-color: transparent transparent transparent #007bff;
}

.custom-prev-arrow::before {
  border-width: 8px 12px 8px 0;
  border-color: transparent #007bff transparent transparent;
}


/* Adaptaciones para pantallas móviles */
@media (max-width: 1100px) {
  .galleria-container {
    width: 100%;
    max-width: 100%;
  }

  .galleria-image {
    object-fit: contain;
  }

  .thumbnailTemplate {
    order: 1; /* Coloca las miniaturas debajo de la imagen principal */
    background-color: transparent; /* Elimina el fondo del contenedor de miniaturas */
    margin-top: 2vh;
  }

  .items-main-gallery {
    order: 0; /* Asegura que la imagen principal esté arriba */
  }
}
  