.checkout-background {
  width: 100%;
  height: 25vh;
  background: linear-gradient(135deg, #000000, #001429);
  display: flex;
  justify-content: center;
  align-items: center;
}

.checkout-background h2 {
  color: white;
  font-size: 3vh;
  letter-spacing: 1px;
  padding-top: 11vh;
}

.checkout-container {
  display: flex;
  justify-content: space-between;
  padding: 4vh 10vh;
  min-height: 80vh;
  gap: 5vw;
}

.checkout-form {
  flex: 0.65;
  background-color: #fff;
  border: 1px solid #333;
  border-radius: 0.4vh;
  padding: 40px;
  display: flex;
  flex-direction: column;
  gap: 1vh;
}

.form-section {
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 0.4vh;
}

.form-section h3 {
  font-size: 2vh;
  background: linear-gradient(135deg, #141414, #001429);
  margin-bottom: 20px;
  font-weight: lighter;
  border-radius: 0.4vh;
  text-transform: uppercase;
  color: white;
  padding: 1vh 2vh;
}


/*INTERNOS DE LOS FORMS*/
.container-names,
.container-phone,
.container-email-dni,
.container-codArea,
.container-state-city,
.container-adress,
.container-facturacion{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 2vh;
  flex-direction: row;
}

.card-names,
.card-phone,
.card-email-dni,
.cardCodArea,
.card-state-city,
.card-adress,
.card-facturacion{
  flex: 1;
  min-width: 20vh;
}


input.p-inputtext#email{
  padding: 1vh;
}

.label-email{
  padding-top: 1vh;
}


/* Flexbox para centrar los elementos dentro de cada input */
.card .p-float-label {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2vh;
  width: 100%;
}

/* Tamaño del input y label */
input.p-inputtext {
  width: 100%;
  padding: 1.5vh;
  transform: translatey(1.6vh);
  font-size: 1.6dvh;
  box-sizing: border-box;
}


label {
  font-size: 1.5vh;
  font-weight: 500;
  line-height: 1.5;
  letter-spacing: 0.1vh;
  color: #555;
  margin-bottom: 0.8vh;
  text-align: left;
  width: 100%;
}

.checkbox-billing{
  margin-bottom: 2vh;
}

/* Añadir un espacio entre el label y el input */
.p-float-label > input + label {
  margin-bottom: 5px;
}

.asterisk {
  color: red;
  margin-left: 2px;
}

/*CITY*/

.mt-2{
  padding: 0.5vh 1vh;
  background-color: #333;
  color: white;
  border-radius: 0.4vh;
  margin-top: 2.8vh;
  border: none;
}

.container-city{
  height: auto;
  display: flex;
  flex-direction: column;
  margin: -1vh 0 3vh;
  gap: 1vh;
}

.container-city .city{
  padding: 1vh;
}

.p-black{
  color: #333;
  margin-top: 1vh;
  margin-bottom: 4vh;
}

.summary-container {
  top: 15vh;
  flex: 0.3;
  background-color: white;
  height: min-content;
  position: sticky;
}

.summary-child-container{
  border-radius: 0.4vh;
  padding: 2dvh;
  border: 1px solid #333;
}

.summary-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.5vh;
  color: #333;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5); /* Fondo semi-transparente */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* Asegúrate de que el modal esté por encima de otros elementos */
}

.modal-content-methods {
  background: white;
  width: 90%;
  border-radius: 0.2vh;
  box-shadow: 0 0.4vh 0.8vh rgba(0, 0, 0, 0.2);
}

.container-methods{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 100%;
  gap: 5vh;
  list-style: none;
  padding: 2vh;
}

.payment-btn{
  border: none;
}

.container-methods li{
  display: flex;
  flex-direction: column;
  gap: 1vh;
}

.container-methods span{
  color: #333;
  font-weight: bold;
}


.image-method{
  object-fit: contain;
  height: 6vh;
  width: 6vh;
}

.close-modal-button {
  width: 100%;
  padding: 0.5vh 1vh;
  background-color: #0056b3; 
  color: #fff; 
  border: none;
  border-radius: 0.2vh;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.close-modal-button:hover {
  background-color: #0056b3;
}

.methods-pay-p{
  font-weight: bold;
  color: #155724;
  margin: 3vh 0 1vh 0;
}

.span-cost-shipping{
  font-weight: bold
}

/* Estilos generales para el botón de ver cuotas */
.view-installments-button {
  font-size: 1.3vh;
  margin-top: 1vh;
  padding: 0.25vh 0.5vh;
  color: #333; 
  border: none;
  border-radius: 0.4vh;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.view-installments-button:hover {
  color: #252525;
  text-decoration: underline;
}

/* Estilos para la sección de cuotas */
.installments-info {
  margin-top: 1dvh;
  padding: 0.7vh;
  background-color: #f9f9f9; /* Fondo claro para el área de cuotas */
  border: 1px solid #ddd; /* Borde sutil */
  border-radius: 0.4vh; /* Bordes redondeados */
}

.installments-info p{
  color: #333;
  font-weight: 550;
  margin-bottom: 0.2vh;
}

.installments-info li {
  color: #333;
  padding: 0.5vh 0;
  border-bottom: 1px solid #e0e0e0; 
}

.installments-info li:last-c4ild {
  border-bottom: none; /* Sin borde en el último elemento */
}

.view-installments-link {
  margin-top: 2vh;
}

.view-installments-link button {
  background-color: #001429;
  color: white;
  padding: 0.4vh 0.5vh;
  cursor: pointer;
  border: none;
  border-radius: 0.25vh;
}

.preview-methods{
  width: 100%;
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 2vh;
  margin-top: 1vh;
}

.image-method-preview{
  object-fit: contain;
  width: 4.26vh;
  height: 4.26vh;
}

.security-message {
  border-radius: 0.2vh;
  margin-top: 4dvh; 
}

.security-message h3 {
  font-size: 1.5vh; 
  color: #155724;
  margin-bottom: 1vh; 
}

.security-message p {
  font-size: 1.3vh; 
  line-height: 1.5;
  color: #212529; 
}

.security-message i {
  margin-right: 8px; 
  font-size: 1.2rem; 
  vertical-align: middle; 
  color: #155724; 
}

.em-checkout{
  font-size: 1.3vh; 
  line-height: 1;
  letter-spacing: 0.05em; 
  color: #212529; 
}

/* */


.product-list-checkout {
  list-style: none;
  padding: 0;
  margin: 0;
  max-width: 100%;
}

.product-list-checkout li {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  border-bottom: 1px solid #ddd;
  font-size: 1.4vh;
  color: #333;
}

.product-list-checkout li span {
  margin-right: 1vh;
}

.product-list-checkout li span:last-child {
  margin-right: 0;
}

.product-list-checkout li span {
  font-weight: normal;
}

.product-list-checkout li span:nth-child(1) {
  font-weight: bold;
}

.product-list-checkout li span:nth-child(2),
.product-list-checkout li span:nth-child(3),
.product-list-checkout li span:nth-child(4) {
  font-style: italic;
  color: #666;
}

.product-list-checkout li span:nth-child(5) {
  font-weight: bold;
  color: #000;
  font-size: 1.6vh;
}

.product-list-checkout li span:first-child {
  text-transform: capitalize;
}


.total-container {
  display: flex;
  justify-content: space-between;
  letter-spacing: 0.05em;
  line-height: 1.6; 
  margin: 1.5vh 0 4vh;
}

.total-container p{
  font-size: 1.7vh;
  letter-spacing: 0.05em; 
  line-height: 1.6; 
  color: #333;
  text-align: center;
}

.container-payment-methods-mobile{
  display: none;
}


@media screen and (max-width: 768px) {
  .checkout-container {
    flex-direction: column-reverse;
    padding: 20px;
  }
  
  .summary-container {
    width: 100%;
    margin-bottom: 20px;
    position: inherit;
  }

  .checkout-form {
    width: 100%;
    padding: 0;
  }

  .view-installments-link {
    display: none;
  }

  .em-checkout{
    display: none;
  }
  
  .methods-pay-p{
    margin: 3vh 0 1.8vh 0;
    font-size: 1.8vh;
  }
  
  .product-list-checkout li {
    font-size: 1.6vh;
    flex-direction: row;
    align-items: flex-start;
  }

  .product-list-checkout li span {
    margin-bottom: 5px;
  }

  .product-list-checkout li span:last-child {
    margin-bottom: 0;
  }

  .container-payment-methods{
    display: none;
  }

  .container-payment-methods-mobile{
    display: inline-block;
    padding: 1vh 2.8vh;
  }

  .mt-2{
    font-size: 1.6vh;
  }
}
